@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Serif:wght@400&family=Poppins:wght@400;600&display=swap");
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: "poppins",sans-serif;

}
html{
    font-size: 62.5%;
}
body{
    background:rgb(219,230,253);
}
h1{
    font-size: 3.2rem;
    margin:2rem 0;
    text-align: center;
}
h2{
    font-family: "IBM Plex serif",serif;
    font-size: 2rem;
}
p{
    margin: 2rem 0;
    color:rgba(21,19,60,0.5);
    font-size: 400;
    font-weight: 1.6rem;
    display: inline-block;
}
span{
    text-transform: capitalize;
    font-weight: bold;
    margin-right:7px;
}
.stories-div
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap:4rem
}
.stories-div p{
font-size:1.3rem;
}
.card{
    min-width: 25rem;
    width:40vw;
    padding:4rem;
    background-color: #ffffff;
    box-shadow: rgba(100,100,111,0.2) 0px 7px 29px 0px;
    border-radius: 0.5rem;
    color:#15133c;
}
.card-button{
    flex-direction:row;
    width:100%;
    display: flex;
    justify-content: space-between;
}
.card-button a{
    color:#92b4ec;
    font-weight: 400;
    font-size: 1.6rem;
    text-decoration: none;
}
.card-button a:last-child{
    color:red;
    text-decoration: none;
}
form{
    display: grid;
    place-items:center;
    margin-top:2rem;
}
input{
    padding:1rem;
    min-width:36rem;
    font-size:2rem;
    text-transform: capitalize;
    border:none;
     border-bottom: 0.2rempx solid #15133c;
     outline:none;
}
.pagination-btn
{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
button{
    padding:0.8rem 2.2rem;
    border:none;
    font-size:1.6rem;
    background-color:#15133c;
    color:#ffffff;
    margin:3rem;
    cursor:pointer;
    display: inline-block;
}